import React , {useState , useEffect , useRef} from 'react';
import './Card.css';
import Detail from './Detail';

const Card=(props)=> {

    const [expand , setexpand ]= useState(false);

    const [cval , uval] = useState({
        title:"",
        content:""
    });

    let menu = useRef();
    useEffect(()=>{
        document.addEventListener("mousedown" , (event)=>{
            if(!menu.current.contains(event.target)){setexpand(false);}
            
        })
    });

    const expandit=()=>{
        setexpand(true);
    }

    const inputItem=(event)=>{
        let {name , value} = event.target;
        uval((preVal)=>{
            return {
                ...preVal,
                [name] : value
            }
        })
        
    }

    const click=()=>{
       
        if(cval.title !=="" && cval.content !==""){
            uval({title:"", content:""});
            props.passNote(cval)
        }  
    }

    const sub=(event)=>{
        event.preventDefault();
    }

  return (
    <div className="main_div" >
    <form onSubmit={sub}>
        <div className="Card" onClick={expandit} ref={menu} >
        {expand?<>
            <input className="inp1" type="text" placeholder="Title" onChange={inputItem} value={cval.title} name="title" autoComplete='off'/>
            <hr style={{width : '100%' , background:'lightgreen'}}/></>:null}
            <input className="inp2" type="text" placeholder="Write a note ..." onChange={inputItem} value={cval.content} name="content" autoComplete='off'/>
            <button type="submit" className="btn_add" onClick={click}>+</button>
        </div>
    </form>
    </div>
  );
}

export default Card;
