import React from 'react';
import './Detail.css';

const Detail=(props)=> {
  return (
    <>
        <div className="Detail">
            <button className="btn_delete" onClick={()=>props.passDeleteNote(props.id)}>x</button>
            <h3>{props.title}</h3>
            <hr style={{width : '100%' , background:'lightgreen'}}/>
            <h5>{props.content}</h5> 
        </div>
    </>
  );
}

export default Detail;
