import React , {useState} from 'react';
import Header from './Header';
import Footer from './Footer';
import Card from './Card';
import Detail from './Detail';

const App=()=> {

  const [carr , uarr] = useState([]);

  const addNote=(note)=>{ 
    uarr((preVal)=>{
      return[...preVal , note];
    })
  }

  const deleteNote=(id)=>{
    // alert("Do you want to delete "+(id+1)+" note")
    uarr((preVal)=>{
      return preVal.filter((value , index)=>{
        return index !==id;
      })
    })
  }

  return (
    <>
    <div className="App">
      <Header/>
      <Card passNote={addNote}/>
      <div className="sub_div">
      {carr.map((value , index)=>{
        return<Detail key={index}
              id={index}
              title={value.title}
              content={value.content}
              passDeleteNote={deleteNote}
              />
      })}
      </div>
      <Footer/>
    </div>
    </>
  );
}

export default App;
